.App {
  text-align: center;
}
.todo__ul{
  list-style-type: none !important;
  padding: 0;
}
.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.todo__ul li {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  align-content: center;
}

.todo__li__span {
  flex: 1 !important;
  text-align: left;
}

.todo__main__div{
  width: 30%;
  margin: 30px auto;
}

.App-link {
  color: #61dafb;
}

.todo__Form form{
  display: flex;
  justify-content: space-between;
}